.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.light-theme{
  --secondary-color: #323232;
  --primary-color: #e9f1fc;
  --redline:#d44147;
  /* #d44147 */
}
.dark-theme{
  --secondary-color: #e9f1fc;
  --primary-color: #323232;
  --redline:#e9f1fc;   
  --themelogo: url('../public/logo192.png');
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
